import { getTheme, ITheme } from "@fluentui/react";

const theme: ITheme = getTheme();

export const statusColor = (isIsolated: boolean, isTested: boolean, type: string = 'color') => {
    let statusValue = 0;

    if (isIsolated) {
        statusValue++;
    }

    if (isTested) {
        statusValue++;
    }

    if (type === 'persona') {
        switch(statusValue) {
            case 2: return 'darkGreen';
            case 1: return 'orange';
            case 0: return 'darkRed';
            default: return 'gray'
        }
    }

    if (type === 'icon') {
        switch(statusValue) {
            case 2: return 'skypeCircleCheck';
            case 1: return 'skypeCircleClock';
            case 0: return 'skypeCircleMinus';
            default: return 'CircleRing'
        }
    }

    switch(statusValue) {
        case 2: return theme.palette.greenDark;
        case 1: return theme.palette.yellowDark;
        case 0: return theme.palette.redDark;
        default: return theme.palette.neutralSecondary;
    }
}