import { Stack, Text, Separator, List, Icon, StackItem, ITheme, getTheme } from '@fluentui/react';
import { Link as RouterLink } from 'react-router-dom'
import { Bar, BarChart, Cell, ResponsiveContainer, XAxis, YAxis } from 'recharts';


import { statusColor } from '../../utils/statusColor';

const segmentList: any[] = [
    { name: 'Samples Received', link: 'received', isIsolated: false, isTested: false, number: 10 },
    { name: 'Samples Isolated', link: 'isolated', isIsolated: true, isTested: false, number: 5 },
    { name: 'Samples Tested', link: 'tested', isIsolated: true, isTested: true, number: 2 },
]
const theme: ITheme = getTheme();

const colors = [theme.palette.red, theme.palette.yellow, theme.palette.green,]

const ProjectListItem = (item: any): JSX.Element => {
    return (
        <Stack
            horizontal
            horizontalAlign='space-between'
            tokens={ { padding: '.25em 0', childrenGap: 10 } }
            verticalAlign='center'>
            <Icon
                iconName={ statusColor(item.isIsolated, item.isTested, 'icon') }
                styles={ { root: { color: statusColor(item.isIsolated, item.isTested) } } }
            />
            <RouterLink
                style={ { flexGrow: 1, } }
                to={ `/vrp-demo/project/a/${ item.link }` }>
                <Text>{ item.name }</Text>
            </RouterLink>
            <Text>
                { item.number }
            </Text>
        </Stack>
    );
};

export const Project: React.FunctionComponent = () => {
    return (        
        <Stack>
            <Stack horizontal horizontalAlign={ 'space-between' }>
                <Text variant={ 'xxLarge' } as='h1' styles={ { root: { margin: 0 } } }>
                    Project A
                </Text>
                <Text variant={ 'xLarge' }>
                    Last Update: 7/12/2021
                </Text>
            </Stack>
            <Separator />
            <Stack 
                horizontal 
                horizontalAlign={ 'space-between' }
                tokens={ { childrenGap: 10 } }
            >
                <StackItem grow={ 1 } styles={ { root: { flexBasis: 0 } } }>
                    <Stack>
                        <Text variant={ 'xLarge' } as='h2'>
                            Summary:
                        </Text>
                        <Text as='h3'>
                            Select a segment to drill down.
                        </Text>
                        <List
                            items={ segmentList }
                            onRenderCell={ ProjectListItem }
                        />
                    </Stack>
                </StackItem>
                <StackItem grow={ 1 } styles={ { root: { flexBasis: 0, height: '50vh' } } }>
                    <ResponsiveContainer width='100%'>
                        <BarChart data={ segmentList }>
                            <XAxis dataKey="name" />
                            <YAxis />
                            <Bar dataKey="number">
                                {segmentList.map((entry, index) => (
                                    <Cell key={`cell-${index}`} fill={colors[index % 20]} />
                                ))}
                            </Bar>
                        </BarChart>
                    </ResponsiveContainer>
                </StackItem>
            </Stack>
        </Stack>
    )
};
