import { ActionButton, Icon, List, Separator, Stack, StackItem, Text } from '@fluentui/react';
import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { statusColor } from '../../utils/statusColor';

const projectList: any[] = [
    { name: 'Project A', id: 'a', isIsolated: false, isTested: false, lastUpdate: '1-1-2020' },
    { name: 'Project B', id: 'b', isIsolated: true, isTested: false, lastUpdate: '1-2-2020' },
    { name: 'Project C', id: 'c', isIsolated: true, isTested: true, lastUpdate: '1-3-2020' },
    { name: 'Project D', id: 'd', isIsolated: true, isTested: true, lastUpdate: '1-4-2020' },
    { name: 'Project E', id: 'e', isIsolated: true, isTested: true, lastUpdate: '1-5-2020' },
]

const DashboardListItem = (item: any): JSX.Element => {
    return (
        <Stack
            horizontal
            horizontalAlign='space-between'
            tokens={ { padding: '.25em 0', childrenGap: 10 } }
            verticalAlign='center'>
            <Icon
                iconName={ statusColor(item.isIsolated, item.isTested, 'icon') }
                styles={ { root: { color: statusColor(item.isIsolated, item.isTested) } } }
            />
            <RouterLink
                style={ { flexGrow: 1, } }
                to={ `/vrp-demo/project/${ item.id }` }>
                <Text>{ item.name }</Text>
            </RouterLink>
            <Text>{ item.lastUpdate }</Text>
        </Stack>
    );
};

export const Dashboard: React.FunctionComponent = () => {
    return (
        <Stack>
            <Text variant={ 'xxLarge' }>
                Welcome, Heath Balcer!
            </Text>
            <Separator />
            <Text variant={ 'xLarge' } as='h1'>
                Your 5 most recent reports:
            </Text>
            <List
                items={ projectList }
                onRenderCell={ DashboardListItem }
            />
            <StackItem align='center'>
                <ActionButton 
                    iconProps={ { iconName: 'ChevronDown' } }
                    styles={ { flexContainer: { flexDirection: 'row-reverse' } } }>
                    Load More
                </ActionButton>
            </StackItem>
        </Stack>
    )
};