const listColumns: IColumn[] = [ { key: 'column1', name: 'Name', minWidth: 0, fieldName: 'name' } ]

export const Tested: React.FunctionComponent = () => {
    const [selectedSample, setSelectedSample]: [ISampleList['id'], Dispatch<SetStateAction<ISampleList['id']>>] = useState(testedList[0].id);
    const [selectedTest, setSelectedTest]: [ITests['id'], Dispatch<SetStateAction<ITests['id']>>] = useState(testedList[0].tests![0].id);

    const initialSelection: Selection = useMemo(() => new Selection(), []);
    const initialSelection2: Selection = useMemo(() => new Selection(), []);

    useEffect(() => {
        initialSelection.setIndexSelected(0, true, false);
        initialSelection2.setIndexSelected(0, true, false);
    }, [initialSelection, initialSelection2]);
    
    return (        
        <Stack>
            <Stack horizontal horizontalAlign={ 'space-between' }>
                <Text variant={ 'xxLarge' } as='h1' styles={ { root: { margin: 0 } } }>
                    Project A
                </Text>
                <Text variant={ 'xLarge' }>
                    Last Update: 7/12/2021
                </Text>
            </Stack>
            <Separator />
            <Text variant={ 'xLarge' } as='h2'>
                Samples Tested
            </Text>
            <Stack 
                horizontal
                horizontalAlign={ 'space-between' }
                tokens={ { childrenGap: 10 } }
                >
                <Stack grow={ 1 } styles={ { root: { flexBasis: 0 } } }>
                    <Text as='h3'>
                        Here is a list of your tested samples:
                    </Text>
                    <DetailsList
                        columns={ listColumns }
                        isHeaderVisible={ false }
                        items={ testedList }
                        onActiveItemChanged={ (item) => { 
                            setSelectedSample(item.id);
                            setSelectedTest(item.tests[0].id)
                            setTimeout(() => initialSelection2.setIndexSelected(0, true, false), 0)
                        } }
                        selectionMode={ SelectionMode.single }
                        selection={ initialSelection }
                    />
                </Stack>
                <Stack grow={ 1 } styles={ { root: { flexBasis: 0 } } }>
                    <Text as='h3'>
                        Here is a list of your { filterSamplesById(testedList, selectedSample).name } test results:
                    </Text>
                    <DetailsList
                        columns={ listColumns }
                        isHeaderVisible={ false }
                        items={ filterSamplesById(testedList, selectedSample).tests! }
                        onActiveItemChanged={ (item) => setSelectedTest(item.id) }
                        selectionMode={ SelectionMode.single }
                        selection={ initialSelection2 }
                    />
                </Stack>
                <Stack grow={ 2 } styles={ { root: { flexBasis: 0 } } }>
                    { selectedSample &&
                        <React.Fragment>
                            <Text as='h3'>
                                { filterSamplesById(filterSamplesById(testedList, selectedSample).tests!, selectedTest).name } Details
                            </Text>
                            <DetailsList 
                                items={ filterSamplesById(filterSamplesById(testedList, selectedSample).tests!, selectedTest).details }
                                isHeaderVisible={ false }
                                selectionMode={ SelectionMode.none }
                            />
                        </React.Fragment>
                    }
                </Stack>
            </Stack>
            { filterSamplesById(filterSamplesById(testedList, selectedSample).tests!, selectedTest).embedUri &&
                <IFrame src={ filterSamplesById(filterSamplesById(testedList, selectedSample).tests!, selectedTest).embedUri! } />
            }
        </Stack>
    )
};
