import React from 'react';
import { ThemeProvider } from '@fluentui/react';
import { Route, Switch } from 'react-router-dom';

import { SideNav, BreadcrumbBar, HeaderBar, FooterBar } from '../';
import { bdTheme } from '../../utils/theme';
import { Dashboard, Isolated, Project, Received, Tested } from '../../views';

import './App.css';


export const App: React.FunctionComponent = () => {
    return (
        <ThemeProvider applyTo='body' theme={ bdTheme }>
            <div className="wrapper">
                <header className="main-head">
                    <HeaderBar />
                </header>
                <div className="main-nav">
                    <SideNav />
                </div>
                <div className="breadcrumb">
                    <BreadcrumbBar />
                </div>
                <main className="content" style={ { padding: '1em' } } data-is-scrollable="true">
                    <Switch>
                        <Route path="/vrp-demo/project/:projectId/received">
                            <Received />
                        </Route>
                        <Route path="/vrp-demo/project/:projectId/isolated">
                            <Isolated />
                        </Route>
                        <Route path="/vrp-demo/project/:projectId/tested">
                            <Tested />
                        </Route>
                        <Route path="/vrp-demo/project/:projectId">
                            <Project />
                        </Route>
                        <Route path="/">
                            <Dashboard />
                        </Route>
                    </Switch>
                </main>
                <footer className="main-footer">
                    <FooterBar />
                </footer>
            </div>
        </ThemeProvider>
    );
};
