import { Icon, Persona, PersonaSize } from "@fluentui/react"
import React from "react"
import { Link as RouterLink } from "react-router-dom"
import { statusColor } from "../../utils/statusColor"

const RouterNavItemCondensed = (props: any) => {
    return (
        <Persona
            hidePersonaDetails={ true }
            initialsColor={ statusColor(props.link.isIsolated, props.link.isTested, 'persona') }
            text={ props.title }
            size={ PersonaSize.size24 }
        />
    )
}

const RouterNavItemExtended = (props: any) => {
    return (
        <React.Fragment>
            { props.iconProps.iconName && 
                <Icon 
                    className='ms-Icon ms-Button-icon' { ...props.iconProps}
                    style={ { fontSize: '16px', margin: '0px 4px', height: '16px', lineHeight: '16px', textAlign: 'center', flexShrink: 0} } /> 
            }
            { props.children }
        </React.Fragment>
    )
}


export const RouterNavLink = (props: any) => {
    return (
        <RouterLink 
            className={ `${props.className} ms-Button ms-Button--action ms-Button--command` }
            style={ { boxSizing: 'border-box', } }
            title={ props.title }
            to={ props.href } >
            <span 
                className='ms-Button-flexContainer'
                style={ { display: 'flex', height: '100%', flexWrap: 'nowrap', justifyContent: 'flex-start', alignItems: 'center', } } >
                
                { props.iconProps.iconName === 'FullCircleMask' ? <RouterNavItemCondensed {...props} /> : <RouterNavItemExtended {...props} /> }

            </span>
        </RouterLink>
    )
}