import { Breadcrumb, getTheme, IBreadcrumbItem, IBreadcrumbStyles, Icon, ITheme, Text } from "@fluentui/react";
import React from "react";
import { Link as RouterLink } from "react-router-dom";

const theme: ITheme = getTheme();

const BreadcrumbStyles: Partial<IBreadcrumbStyles> = {
    root: {
        borderBottom: `1px solid ${ theme.palette.neutralQuaternary }`,
        padding: '0px 1em',
        height: '28px',
    },
    chevron: {
        padding: '0 .5em',
    },
    listItem: {
        fontWeight: 'bold',
    }
}

const navItems: IBreadcrumbItem[] = [
    {
        href: '/',
        key: '0',
        text:'Dashboard',
    },
    {
        href: '/vrp-demo/project/a',
        key: 'A',
        text:'Project A',
    },
    {
        href: '/vrp-demo/project/a/isolation',
        key: 'A1',
        text:'Isolation',
    },
]

const BreadcrumbLink = (props: any) => {
    return (
        <RouterLink
            to={ props.href }
            style={ { textDecoration: 'none', } } >
            { props.key === "0" &&
                <Icon 
                    iconName='Home'
                    style={ { paddingRight: '.25em', position: 'relative', top: '2px' } }
                /> 
            }
            <Text>
                { props.text }
            </Text>
        </RouterLink>
    );
}

export const BreadcrumbBar: React.FunctionComponent = () => {
    return (
        <Breadcrumb
            focusZoneProps={{ handleTabKey: 1 }}
            items={ navItems }
            maxDisplayedItems={ 3 }
            onRenderItem={ BreadcrumbLink }
            styles={ BreadcrumbStyles }
        />
    )
};